.campo{
    margin:24px 0px;
    font-family: 'Montserrat', sans-serif;
}
.campo label{
    font-size: 18px;
    font-weight: 600;
    display: block;
    margin-bottom: 8px;
}
.campo input{
    width: 100%;
    background-color: #FFFF;
    box-shadow: 10px 10px 30px rgba(0,0,0,.08);
    border: none;
    padding: 30px 20px;
    outline-color: #6278F7;
    font-size: 1rem;
    box-sizing: border-box;
}

.campo-color input{
    height: 40px;
    padding: 5px 20px;
}